@use 'colors' as *
@use 'typographie' as *
@use 'mixins' as *

.presentation
    &__texte
        &--emphase
            font-family: $robotoMono-italic
            @include glow-effect($darkTurquoise, $cyan)

.identite
    &__champ
        @include text_font(2rem, $cyan )
        
    &__texte
        @include text_font(1.5rem, $brightGray)


.journal
    &__carte
        border: 2px solid $cyan
        padding: 15px
        border-radius: 0px 25px

.audio-player__time
    @include text_font(1.2rem, $brightGray)
    font-style: italic
